@import '../../helpers/helpers.scss';

.profileTools {
  background-color: #fff;
  overflow: hidden;

  border: 1px solid #eee;
  box-shadow: 0 4px 3px  rgba($color: #000, $alpha: .2);

  border-radius: 8px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: max-content;

  &__item {
    display: flex;
    align-items: center;
    height: min-content;
    // justify-content: center;
    padding: 5px;
    padding-right: 15px;

    &-title {
      font-size: 10px;

      @include tablet() {
        font-size: 12px;
      }

      @include desktop() {
        font-size: 14px;
      }
    }


  &-img {
    width: 12px;
    height: 12px;
    background-size: contain;
    background-repeat: no-repeat;
    margin-right: 5px;

    @include tablet() {
      width: 16px;
      height: 16px;
    }
  
    @include desktop() {
      width: 20px;
      height: 20px;
    }
  }

    &:hover {
        background-color: #eee;
    }
  }
}